const UA = (window.navigator.userAgent || navigator.userAgent).toLowerCase();

/**
 * 提供各种工具方法
 * @type {{*}}
 */

module.exports = {
  isTj: function() {
    return UA.indexOf("closer-ios") > -1 || UA.indexOf("closer-android") > -1
  },
  callTjBridge: function(funName, params) {
    console.log("call:", funName, "---", params)
    if (funName) {
      try {
        if (UA.indexOf("closer-ios") > -1) {
          if (window.WebViewJavascriptBridge) {
            setupWebViewJavascriptBridge(function(bridge) {
              if (params) {
                bridge.callHandler(funName, params, function(res) {
                  params.success && params.success(res)
                });
              } else {
                bridge.callHandler(funName, function(res) {});
              }
            });
          }
        } else if (UA.indexOf("closer-android") > -1) {
          if (typeof window.bridge != "undefined") {
            if (params) {
              params.success && params.success(window.bridge[funName](params));
            } else {
              window.bridge[funName]();
            }
          }
        }
      } catch (e) {
        options.fail && options.fail(e);
      }

    }

  }

}